<template>
  <div class="manager_iframe">
    <table class="layui-table">
      <colgroup>
        <col width="150">
        <col width="200">
        <col width="100">
        <col width="100">
      </colgroup>
      <thead>
      <tr class="feedback_tr">
        <th class="layui-hide-xs">openId</th>
        <th class="feedback_td">信息</th>
        <th class="layui-hide-xs">日期</th>
        <th>管理</th>
      </tr>
      </thead>
      <tbody>
      <tr class="feedback_tr" v-for="(each,index) in feedItems" :key="index">
        <td class="layui-hide-xs">{{each.openid.slice(0,10)}}</td>
        <td class="feedback_td" @click="showContent(each.content)">{{each.content.slice(0,14)}}</td>
        <td class="layui-hide-xs">{{each.createTime}}</td>
        <td>
          <button class="layui-btn layui-btn-normal layui-btn-xs" @click="returnMsg(each.openid)">回复</button>
          <button class="layui-btn layui-btn-danger layui-btn-xs layui-hide-xs" @click="deleteMsg">删除</button>
        </td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script scoped>
    export default {
        name: "FeedBack",
      data(){
          return{
            feedItems: []
          }
      },
      mounted: function() {
        //console.log(this);
        document.getElementById("preloader").style.display = "block";
        axios.get("/manager/feedback")
          .then(response => {
            let temp = response.data.data;
            temp.forEach((item, index) => {
              item.createTime = this.format(item.createTime);
            })
            this.feedItems = temp;
            document.getElementById("preloader").style.display = "none";
          });
      },
      updated: function() {
        // console.log(this.feedItems,'渲染完毕');
      },
      methods: {
        format: function(time) {
          function formatNumber(n) {
            const str = n.toString()
            return str[1] ? str : `0${str}`
          }
          let date = new Date();
          date.setTime(time * 1000);
          const month = date.getMonth() + 1
          const day = date.getDate()
          const hour = date.getHours()
          const minute = date.getMinutes()
          const t1 = [month, day].map(formatNumber).join('-')
          const t2 = [hour, minute].map(formatNumber).join(':')
          return `${t1}-${t2}`
        },
        deleteMsg: function() {
          layer.msg('暂未定义');
        },
        showContent(content) {
          layer.open({
            title: '详情',
            content: content
          });
        },
        returnMsg(openId) {
          layer.prompt({
            title: `发送给: ${openId}`,
            formType: 2,
            value: "内师助手:",
            area: ['340px', '200px']
          }, function(text, index) {
            layer.close(index);
            let params = {
              openId,
              text
            }
            $.post("/manager/feedback", params, (data) => {
              if (!data.status) {
                layer.msg("发送成功");
              } else {
                layer.msg("发送失败");
              }
            }, "json");

          });
        }
      }
    }
</script>




