<template>
    <div class="manager_iframe">

        <form class="layui-form" action="" id="List">
          <div class="layui-form-item">
            <input type="text" name="title" required  lay-verify="required" placeholder="请输入文章标题" autocomplete="off" maxlength="20" class="layui-input">
          </div>
          <div class="layui-form-item">
            <input type="text" name="path"  placeholder="这里输入图片地址" autocomplete="off" class="layui-input">
          </div>
          <div class="layui-form-item">
            <input type="text" name="author" required  lay-verify="required" placeholder="这里输入作者名字" autocomplete="off" maxlength="10" class="layui-input">
          </div>


          <div class="layui-form-item" id="editor">
            <p>请一定注意你发的内容</p>
          </div>

          <div class="layui-form-item">
            <button class="layui-btn layui-btn-primary layui-btn-fluid" lay-submit lay-filter="formDemo">立即提交</button>
          </div>
        </form>
    </div>
</template>

<script>
  import wangEditor from "../../static/editor/wangEditor.min.js"
    export default {
        name: "Edit",
      mounted(){
        var E = wangEditor
        var editor = new E('#editor')
        editor.customConfig.pasteFilterStyle = false
        editor.customConfig.pasteIgnoreImg = false
        // 或者 var editor = new E( document.getElementById('editor') )
        editor.create()
        layui.use(['form','jquery'], function(){
          var form = layui.form;
          var $ =layui.jquery;

          //监听提交
          form.on('submit(formDemo)', function(data){
            console.log(data);
            let date = new Date().toLocaleDateString();
            data.field.date=date;
            data.field.content=editor.txt.html();
            $.post('/manager/artinsert',data.field,(data)=>{

              if (!data.status){
                layer.msg('文章上传成功!');
                document.getElementById("List").reset();
                editor.txt.clear();
                // setTimeout(()=> window.open('/manager/',"_self"),1000) ;
              }else{
                layer.msg('文章上传失败!');
              }

            });
            // layer.msg(JSON.stringify(data.field));
            return false;
          });
        });
      }
    }
</script>




