<template>
  <div class="manager_iframe">
    <div class="cells user_margin">
      <div class="weui-cell" >
        <div class="weui-cell__hd" ><label class="weui-label">账号:</label></div>
        <div class="weui-cell__bd">
          <p class="" id="user">{{userName}}</p>
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd"><label class="weui-label">修改密码</label></div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="password" pattern="[\w]*" placeholder="请输入密码" v-model="passwdSecond"/>
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd"><label class="weui-label">确定修改</label></div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="password" pattern="[\w]*" placeholder="请输入密码" v-model="passwdFirst"/>
        </div>
      </div>
      <a class="layui-btn layui-btn-primary layui-btn-fluid" href="javascript:" id="showTooltips" @click="passwdChange">确定</a>
    </div>
    <!--BEGIN toast-->
    <div id="toast" style="display: none;">
      <div class="weui-mask_transparent"></div>
      <div class="weui-toast">
        <i class="weui-icon-success-no-circle weui-icon_toast"></i>
        <p class="weui-toast__content">更改完成</p>
      </div>
    </div>
    <!--end toast-->
    <!-- loading toast -->
    <div id="loadingToast" style="display:none;">
      <div class="weui-mask_transparent"></div>
      <div class="weui-toast">
        <i class="weui-loading weui-icon_toast"></i>
        <p class="weui-toast__content">正在更改中</p>
      </div>
    </div>

  </div>
    
</template>

<script>
  import "../../static/css/weui.min.css"
    export default {
      name: "ChangePwd",
      props:['userName'],
      data(){
          return{
            myoptions: [],
            passwdFirst: '',
            passwdSecond: ''
          }
      },
      mounted: function() {
        $(function() {
          var $iosActionsheet = $('#iosActionsheet');
          var $iosMask = $('#iosMask');

          function hideActionSheet() {
            $iosActionsheet.removeClass('weui-actionsheet_toggle');
            $iosMask.fadeOut(200);
          }

          $iosMask.on('click', hideActionSheet);
          $('#iosActionsheetCancel').on('click', hideActionSheet);
          $("#showIOSActionSheet").on("click", function() {
            $iosActionsheet.addClass('weui-actionsheet_toggle');
            $iosMask.fadeIn(200);
          });
        });
      },
      methods:{
        passwdChange: function() {
          if (this.passwdFirst == this.passwdSecond && this.passwdFirst.length > 0) {
            let $toast = $('#toast');
            let $loadingToast = $('#loadingToast');
            if ($loadingToast.css('display') != 'none') return;
            $loadingToast.fadeIn(100);
            let user = this.userName;
            let params = {
              'user': user,
              'passwd': this.passwdSecond
            }
            $.post('/manager/changePwd', params, (data) => {
              $loadingToast.fadeOut(100);
              if (!data.status) {
                $toast.fadeIn(50);
                setTimeout(() => {
                  $toast.fadeOut(25);
                }, 500);
              } else {
                layer.msg('修改不成功');
              }
            });
          } else {
            layer.msg('密码不一致,且不能为空');
          }
        }
      }
    }
</script>




