<template>
  <div>
    <div id="preloader" style="display: none;">
      <div id="loader"></div>
    </div>
    <manager-header  @change="changepwd" :userName="userName"></manager-header>
    <div class="layui-container manager_custom" id="manager">
      <div class="layui-tab layui-tab-brief layui-show-xs-block layui-hide-sm manager_tabbar" >
        <ul class="layui-tab-title">
          <li v-for="(each,index) in managerList" :key="index" :class="each.status?'layui-this':''" @click="focus(index)"><a :href="each.href">{{ each.name}}</a></li>
        </ul>
      </div>
      <!-- 左侧区域 -->
      <div class="manager_left layui-hide-xs">
        <div class="manager_block">
          <a v-for="(each,index) in managerList" :key="index" class="manager_item" :class="each.status?'manager_item_focus':''" :href="each.href" @click="focus(index)">
            {{ each.name}}
          </a>

        </div>
        <div class="manager_block">
          <a class="manager_item manager_name" href="javascript:;" >内测功能</a>
        </div>
      </div>
      <!-- 右侧区域 -->
      <div class="manager_right">
        <div class="manager_content">
          <options v-if="managerId=='v0'" options></options>
          <map-sign v-if="managerId=='v1'" ></map-sign>
          <article-manager v-if="managerId=='v2'"></article-manager>
          <edit v-if="managerId=='v3'"></edit>
          <feed-back v-if="managerId=='v4'"></feed-back>
          <advertisement v-if="managerId=='v5'"></advertisement>
          <ad-manager v-if="managerId=='v6'"></ad-manager>
          <keyword v-if="managerId=='v7'"></keyword>
          <change-pwd v-if="managerId=='root'" :userName="userName"></change-pwd>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import "../../static/css/manager.css"
  import "../../static/css/loading.css"
  import "../../static/css/global.css"
  import ManagerHeader from "@/components/ManagerHeader"
  import FeedBack from "@/components/FeedBack"
  import MapSign from "@/components/Map"
  import Edit from "@/components/Edit"
  import ArticleManager from "@/components/ArticleManager"
  import Options from "@/components/Options"
  import ChangePwd from "@/components/ChangePwd"
  import Advertisement from "@/components/Advertisement"
  import AdManager from "@/components/AdManager"
  import Keyword from "@/components/KeywordManager"
    export default {
      name: "Manager",
      data(){
        return{
          managerList: [{
            name: '模块开关',
            href: 'javascript:;',
            status: true
          }, {
            name: '地图标记',
            href: 'javascript:;',
            status: false
          }, {
            name: '文章管理',
            href: 'javascript:;',
            status: false
          }, {
            name: '文章上传',
            href: 'javascript:;',
            status: false
          }, {
            name: '客服反馈',
            href: 'javascript:;',
            status: false
          }, {
              name: '广告添加',
              href: 'javascript:;',
              status: false
          }, {
            name: '广告管理',
            href: 'javascript:;',
            status: false
          },  {
            name: '关键词',
            href: 'javascript:;',
            status: false
          },{
            name: '待添加',
            href: 'javascript:;',
            status: false
          }],
          managerId:"v0",
          userName:""
        }
      },
      components:{
        ManagerHeader,
        FeedBack,
        MapSign,
        Edit,
        ArticleManager,
        Options,
        ChangePwd,
        Advertisement,
        AdManager,
        Keyword
      },
      mounted(){
        this.getInfo()
      },
      methods:{
        focus(index) {
          this.managerId=`v${index}`
          console.log(this.managerId)
          this.managerList.map((item, id) => {
            id == index ? item.status = true : item.status = false
          })
        },
        changepwd(e){
          this.managerId=`root`
        },
        getInfo(){
          axios.get("/manager/info")
            .then(response => {
              this.userName = response.data.data;
            });
        }
      }
    }
</script>

<style scoped>

</style>



