<template>
  <div class="manager_iframe">
    <table class="layui-table" id="info">
      <colgroup>
        <col width="200">
        <col width="100">
        <col width="100">
        <col width="150">
      </colgroup>
      <thead>
      <tr>
        <th class="">备注</th>
        <th>开始时间</th>
        <th class="layui-hide-xs">结束时间</th>
        <th>管理</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(each,index) in items" :key="index">
        <td class="">{{each.remark.slice(0,15)}}</td>
        <td>{{each.dateStart}}</td>
        <td class="layui-hide-xs">{{each.dateEnd}}</td>
        <td class="">
          <button class="layui-btn layui-btn-normal layui-btn-xs layui-hide-xs" @click="editItem">修改</button>
          <button class="layui-btn layui-btn-danger layui-btn-xs" @click="deleteItem(each.ID,index)">删除</button>
        </td>
      </tr>
      </tbody>
    </table>
  </div>

</template>

<script>
  export default {
    name: "AdManager",
    data() {
      return{
        items: ''
      }
    },
    mounted(){
      document.getElementById("preloader").style.display = "block";
      axios.get("/manager/adlist")
        .then(response => {
          this.items = response.data.data;
          document.getElementById("preloader").style.display = "none";
        });

    },
    updated: function() {
      console.log(this.items, '渲染完毕');
    },
    methods: {
      deleteItem: function(e, index) {
        var that = this;
        let id = e;
        let url = "/manager/delete?type=ads&id="+id;
        setTimeout(() => {
          axios.post(url)
            .then(response => {
              if (!response.data.status) {
                this.items.splice(index, 1);
                layer.msg(response.data.message);
              }
              console.log(response.data);
            });
        }, 500);

      },
      editItem: function() {
        layer.msg('暂未定义');
      }
    }
  }
</script>

<style scoped>

</style>



