<template>
    <div class="manager_iframe">
      <div class="weui-cells__title options_top">功能开关项</div>
      <div class="weui-cells weui-cells_form ">
        <div class="weui-cell weui-cell_switch "  v-for="(each,index) in myoptions" :key="index">
          <div class="weui-cell__bd" >{{each.name}}</div>
          <div class="weui-cell__ft">
            <label :for="'option'+index" class="weui-switch-cp">
              <input :id="'option'+index" class="weui-switch-cp__input"  v-if="each.status" checked="checked" type="checkbox"  :value="each.option_id"   @click="check"/>
              <input :id="'option'+index" class="weui-switch-cp__input"  v-else type="checkbox"  :value="each.option_id"   @click="check"/>
              <div class="weui-switch-cp__box"></div>
            </label>
          </div>
        </div>
      </div>
      <!--BEGIN toast-->
      <div id="toast" style="display: none;">
        <div class="weui-mask_transparent"></div>
        <div class="weui-toast">
          <i class="weui-icon-success-no-circle weui-icon_toast"></i>
          <p class="weui-toast__content">已完成</p>
        </div>
      </div>
    </div>
</template>

<script>
  import "../../static/css/weui.min.css"
    export default {
      name: "Options",
      data() {
        return{
          myoptions: []
        }
      },
      mounted: function() {
        //console.log(this);
        document.getElementById("preloader").style.display = "block";
        axios.get("/api/indexconfig")
          .then(response => {
            this.myoptions = response.data.data;
            document.getElementById("preloader").style.display = "none";
          });
      },
        methods: {
          check: function(e) {
            //console.log(e.toElement.checked);
            //console.log(option_id);
            let $toast = $('#toast');

            if ($toast.css('display') != 'none') return;

            $toast.fadeIn(25);

            let option_id = e.toElement.id;
            let option_status = e.toElement.checked;
            let option_index = $("#" + option_id).attr("value");
            var params = new URLSearchParams();
            params.append('status', Number(option_status));
            params.append('id', option_index);
            setTimeout(() => {
              axios.post("/manager/option", params, {
                headers: {
                  'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
                }
              })
                .then(response => {
                  console.log(response);
                  $toast.fadeOut(25);
                });
            }, 1000);
          }
        }
    }
</script>




