<template>
  <div class="manager_iframe">

    <form class="layui-form" action="" id="List">
      <div class="layui-form-item">
        <label class="layui-form-label">UUID:</label>
        <div class="layui-input-block">
          <select name="uuid" lay-verify="required">
            <option value=""></option>
            <option v-for="(each,index) in items " :key="index" :value="each.ID">{{each.title}}</option>
          </select>
        </div>
      </div>
      <div class="layui-form-item">
        <label class="layui-form-label">ImgURL:</label>
        <div class="layui-input-block">
          <input type="text" name="path"  placeholder="这里输入图片地址" autocomplete="off" class="layui-input">
        </div>
      </div>
      <div class="layui-form-item">
        <label class="layui-form-label">Remark:</label>
        <div class="layui-input-block">
          <input type="text" name="remark"  placeholder="这里输入备注" autocomplete="off" class="layui-input" maxlength="18">
        </div>
      </div>
      <div class="layui-form-item">
        <div class="layui-inline">
          <label class="layui-form-label">Range:</label>
          <div class="layui-input-inline" style="width: 100px;">
            <input type="text" name="dateStart" required  lay-verify="required" class="layui-input" placeholder="Date" id="selectDateStart">
          </div>
          <div class="layui-form-mid">-</div>
          <div class="layui-input-inline" style="width: 100px;">
            <input type="text" name="dateEnd" required  lay-verify="required" class="layui-input" placeholder="Date" id="selectDateEnd">
          </div>
        </div>
      </div>

      <div class="layui-form-item">
        <button class="layui-btn layui-btn-primary layui-btn-fluid" lay-submit lay-filter="formDemo">立即提交</button>
      </div>
    </form>
  </div>
</template>

<script>
  export default {
    name: "Advertisement",
    data(){
      return{
        items:[]
      }
    },
    mounted(){
      document.getElementById("preloader").style.display = "block";
      axios.get("/api/artlist")
        .then(response => {
          this.items = response.data.data;
          document.getElementById("preloader").style.display = "none";
        });


    },
    updated(){
      layui.use(['form','jquery','laydate'], function(){
        var form = layui.form;
        var $ =layui.jquery;
        var laydate = layui.laydate;
        form.render();
        laydate.render({
          elem: '#selectDateStart' //指定元素
        });
        laydate.render({
          elem: '#selectDateEnd' //指定元素
        });
        //监听提交
        form.on('submit(formDemo)', function(data){
          console.log(data);
          console.log(data.field)
          $.post('/manager/adinsert',data.field,(data)=>{
            if (!data.status){
              layer.msg('广告添加成功!');
              document.getElementById("List").reset();
              // setTimeout(()=> window.open('/manager/',"_self"),1000) ;
            }else{
              layer.msg('广告添加失败');
            }

          });
          // layer.msg(JSON.stringify(data.field));
          return false;
        });
      });
    }
  }
</script>




