<template>
  <div class="manager_iframe">
    <form class="layui-form" action="" id="keywords">
      <div class="layui-form-item">
        <div class="layui-inline">

          <div class="layui-input-inline">
            <input type="text" name="keyword" required lay-verify="required" placeholder="请输入关键词" autocomplete="off" class="layui-input">
          </div>

          <div class="layui-input-inline">
            <input type="text" name="content" required lay-verify="required" placeholder="请输入响应内容" autocomplete="off" class="layui-input">
          </div>

          <button class="layui-btn layui-btn-primary" lay-submit lay-filter="insertKeyword">添加数据</button>
        </div>
      </div>
    </form>
    <table class="layui-table" id="info">
      <colgroup>
        <col width="100">
        <col width="200">
        <col width="100">
      </colgroup>
      <thead>
      <tr>
        <th class="">关键词</th>
        <th>响应内容</th>
        <th>管理</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(each,index) in items" :key="index">
        <td class="">{{each.keyword}}</td>
        <td @click="showContent(each.content)">{{each.content.slice(0,28)}}</td>
        <td class="">
          <button class="layui-btn layui-btn-normal layui-btn-xs layui-hide-xs" @click="editItem">修改</button>
          <button class="layui-btn layui-btn-danger layui-btn-xs" @click="deleteItem(each._id,index)">删除</button>
        </td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
    export default {
      name: "KeywordManager",
      data() {
        return{
          items: []
        }
      },
      mounted(){
        document.getElementById("preloader").style.display = "block";
        axios.get("/manager/keywordlist")
          .then(response => {
            this.items = response.data.data;
            document.getElementById("preloader").style.display = "none";
          });

      },
      updated(){
        var that=this
        layui.use(['form','jquery'], function(){
          var form = layui.form;
          var $ =layui.jquery;
          form.render();
          //监听提交
          form.on('submit(insertKeyword)', function(data){
            console.log(data);
            console.log(data.field)
            $.post('/manager/keywordinsert',data.field,(response)=>{
              if (!response.status){
                layer.msg('广告添加成功!');
                document.getElementById("keywords").reset();
                console.log(that.items)
                that.items.unshift(data.field)
              }else{
                layer.msg('广告添加失败');
              }

            });
            // layer.msg(JSON.stringify(data.field));
            return false;
          });
        });
      },
      methods: {
        deleteItem: function(e, index) {
          let id = e;
          let url = "/manager/delete?type=keyword&id="+id;
          setTimeout(() => {
            axios.post(url)
              .then(response => {
                if (!response.data.status) {
                  this.items.splice(index, 1);
                  layer.msg(response.data.message);
                }
                console.log(response.data);
              });
          }, 500);

        },
        showContent(content) {
          layer.open({
            title: '详情',
            content: content
          });
        },
        editItem: function() {
          layer.msg('暂未定义');
        }
      }
    }
</script>

<style scoped>

</style>



