<template>
  <div class="manager_iframe">
    <div class="layui-btn-container iframe_center">
      <button class="layui-btn  layui-btn-radius" :class="!select?'layui-btn-danger':'layui-btn-primary'"  @click="change('saihan')" >赛罕校区</button>
      <button class="layui-btn  layui-btn-radius" :class="select?'layui-btn-danger':'layui-btn-primary'"  @click="change('shengle')" >盛乐校区</button>
    </div>

    <div class="layui-tab" id="info">
      <ul class="layui-tab-title">
        <li class="layui-this">教学楼</li>
        <li>学生公寓</li>
        <li>生活相关</li>
      </ul>
      <div class="layui-tab-content">
        <div class="layui-tab-item" v-for="(item,index) in items" :key="index" :class="index==0?'layui-show':''" >
          <table class="layui-table" >
            <colgroup>
              <col width="150">
              <col width="150">
              <col width="150">
              <col width="100">
            </colgroup>
            <thead>
            <tr>
              <th>位置名称</th>
              <th>经度</th>
              <th>维度</th>
              <th>管理</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(each,num) in item" :key="num">
              <td>{{each.content}}</td>
              <td>{{each.longitude}}</td>
              <td>{{each.latitude}}</td>
              <td class="">
                <button class="layui-btn layui-btn-normal layui-btn-xs" @click="editItem(each,index,num)">修改</button>
                <button class="layui-btn layui-btn-danger layui-btn-xs" @click="deleteItem(each,index,num)">删除</button>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
    
</template>

<script>
    export default {
      name: "Map",
      data() {
          return{
            items: '',
            temp: [],
            select: 0
          }
      },
      beforeCreate: function() {
        //console.log(this);
        document.getElementById("preloader").style.display = "block";
        axios.get("/api/point")
          .then(response => {
            let _response=response.data;
            // console.log(response.data);
            this.temp = _response.data.mapSign;
            this.items = _response.data.mapSign.saihan;
            document.getElementById("preloader").style.display = "none";
          });
      },
      updated: function() {
        // console.log(this.items,'渲染完毕');
      },
      methods: {
        change(e) {
          if (e == "shengle") {
            this.items = this.temp.shengle;
            this.select = 1;
          } else {
            this.items = this.temp.saihan;
            this.select = 0;
          }
        },
        deleteItem: function(info, index, num) {
          var that = this;
          let type = !this.select ? "saihan" : "shengle";
          let con = info.content;
          let lon = info.longitude;
          let id = index;
          let params = {
            "type": type,
            "id": id,
            "content": con,
            "longitude": lon,
            "digest":"pull"
          };
          $.post('/api/point', params, (data) => {
            if (!data.status) {
              layer.msg('删除标记成功');
              that.items[index].splice(num, 1);
            } else {
              layer.msg('删除标记失败!');
            }
          });
        },
        editItem: function(info, index, num) {
          var that = this;
          let type = !this.select ? "saihan" : "shengle";
          let content = info.content;
          let con = info.content;
          let lon = info.longitude;
          let id = index;
          let params = {
            "type": type,
            "id": id,
            "content": con,
            "longitude": lon,
            "digest":"set"
          };
          layer.prompt({
            title: `确定修改地标名: ${content}`,
            formType: 0,
            value: content,
          }, function(text, index) {
            layer.close(index);
            params.newName = text;
            that.items[id][num].content = text;
            $.post('/api/point', params, (data) => {
              if (!data.status) {
                layer.msg('更改名字成功');
              } else {
                layer.msg('更改名字失败!');
              }
            });

            // layer.msg('您最后写下了：'+text);
          });

        }
      }
    }
</script>




