<template>
  <div class="manager_iframe">
    <table class="layui-table" id="info">
      <colgroup>
        <col width="150">
        <col width="200">
        <col width="100">
        <col width="100">
      </colgroup>
      <thead>
      <tr>
        <th class="layui-hide-xs">作者</th>
        <th>标题</th>
        <th class="layui-hide-xs">日期</th>
        <th>管理</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(each,index) in items" :key="index">
        <td class="layui-hide-xs">{{each.author}}</td>
        <td>{{each.title}}</td>
        <td class="layui-hide-xs">{{each.date}}</td>
        <td class="">
          <button class="layui-btn layui-btn-normal layui-btn-xs layui-hide-xs" @click="editItem(each.ID,each.content)">修改</button>
          <button class="layui-btn layui-btn-danger layui-btn-xs" @click="deleteItem(each.ID,index)">删除</button>
        </td>
      </tr>
      </tbody>
    </table>
  </div>
    
</template>

<script>
    export default {
        name: "ArticleManager",
      data() {
          return{
            items: ''
          }
      },
      mounted(){
        document.getElementById("preloader").style.display = "block";
        axios.get("/api/artlist")
          .then(response => {
            this.items = response.data.data;
            document.getElementById("preloader").style.display = "none";
          });

      },
      updated: function() {
        console.log(this.items, '渲染完毕');
      },
      methods: {
        deleteItem: function(e, index) {
          var that = this;
          let id = e;
          let url = "/manager/delete?type=article&id="+id;
          setTimeout(() => {
            axios.post(url)
              .then(response => {
                if (!response.data.status) {
                  this.items.splice(index, 1);
                  layer.msg(response.data.message);
                }
                console.log(response.data);
              });
          }, 500);

        },
        editItem: function(id, content) {
          layer.msg('暂未定义');
          /*     console.log(id)
          layer.prompt({
              title: `文章Uuid: ${id}`,
              formType: 2,
              value: content,
              area: ['800px', '350px']
          }, function(text, index){
              layer.close(index);
              layer.msg('<br>您最后写下了：'+text);
          });
*/
        }
      }
    }
</script>

<style scoped>

</style>



