<template>
  <div class="header_custom layui-header">
    <div class="layui-main">
      <a class="logo" href="/">
        <img src="/static/logo.png" alt=""></a>
      <ul class="layui-nav" lay-filter="">
        <li class="layui-nav-item layui-hide-xs"><a href="http://mp.weixin.qq.com/s/KOydGJa7D3dJzl9fvOUTQg">微信公众号</a></li>
        <li class="layui-nav-item layui-hide-xs"><a href="https://github.com/strugglerx">Github</a></li>
        <li class="layui-nav-item layui-hide-xs"><a href="https://weibo.com/577973344">微博</a></li>
        <li class="layui-nav-item layui-hide-xs"><a href="/">返回首页</a></li>
        <li class="layui-nav-item">
          <a href="javascript:;"><img src="http://www.yougexing.net/uploads/180720/1-1PH0160022.jpg" class="layui-nav-img">{{userName}}</a>
          <dl class="layui-nav-child">
            <dd><a href="javascript:;" @click="changepwd" >更改用户</a></dd>
            <dd><a href="javascript:;" @click="logout">注销</a></dd>
          </dl>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
    export default {
      name: "ManangerHeader",
      props:['userName'],
      methods:{
        logout(){
            axios.get("/logout")
              .then(response => {
                if (!response.data.status) {
                  layer.msg(response.data.message);
                  setTimeout(() => open("/","_parent"), 500);
                }
              });
        },
        changepwd(){
          this.$emit("change",this.userName)
        }


      }
    }
</script>





